import { Action } from '@ali/4ever-cangjie';
import { ITableSelection } from '../types';
import Table from '../../mo/models';

export const TABLE_SELECT_SELECTIONS = Symbol('TABLE_SELECT_SELECTIONS');

export function selectTableSelection(table, selection, callback = null) {
  return {
    type: TABLE_SELECT_SELECTIONS,
    payload: {
      node: table,
      selection,
      callback,
    },
  };
}

interface TableSelectTableSelectionPayload {
  node: Table;
  selection: ITableSelection;
  callback: null | Function;
}

interface TableSelectTableSelectionAction {
  type: typeof TABLE_SELECT_SELECTIONS;
  payload: TableSelectTableSelectionPayload;
}

export function isTableSelectTableSelectionAction(
  action: Action,
): action is TableSelectTableSelectionAction {
  return action.type === TABLE_SELECT_SELECTIONS;
}
